import React, { useState } from "react";
import { FaEdit, FaTrash } from "react-icons/fa";
import Status from "../../Component/Status";
import { useDispatch, useSelector } from "react-redux";
import {
  deleteBranch,
  getPageBranch,
  getPrevious,
  getNext,
} from "../../Redux/Branch/thunk";
import { branchConstants } from "../../Redux/Branch/constants";
import PaginationBlock from "../../Component/PaginationBlock";

import NoData from "../../Component/NoData";
import Tooltip from "../../Component/Tooltip/Tooltip";
import { useHistory } from "react-router-dom";
const BranchList = ({
  currentPage,
  postsPerPage,
  setCurrentPage,
  count,
  setShowModal,
  maxPageNumberLimit,
  setMaxPageNumberLimit,
  minPageNumberLimit,
  setMinPageNumberLimit,
  search,
}) => {
  const history = useHistory();
  // props
  const group = useSelector((state) => state.auth.group);
  const is_superuser = useSelector((state) => state.auth.is_superuser);
  const branches = useSelector((state) => state.branch.branches);
  const next = useSelector((state) => state.branch.next);
  const previous = useSelector((state) => state.branch.previous);
  const dispatch = useDispatch();

  // for pagination
  const [pageNumberLimit] = useState(5);

  //change page
  const paginate = (number) => {
    dispatch(getPageBranch({ number, postsPerPage }));
    setCurrentPage(number);
  };

  //handle Click
  const handleClick = (type) => {
    if (type === "previous") {
      dispatch(getPrevious(previous));
      setCurrentPage((prevState) => prevState - 1);
      if ((currentPage - 1) % pageNumberLimit === 0) {
        setMaxPageNumberLimit(maxPageNumberLimit - pageNumberLimit);
        setMinPageNumberLimit(minPageNumberLimit - pageNumberLimit);
      }
    } else if (type === "next") {
      dispatch(getNext(next));
      setCurrentPage((prevState) => prevState + 1);
      if (currentPage + 1 > maxPageNumberLimit) {
        setMaxPageNumberLimit(maxPageNumberLimit + pageNumberLimit);
        setMinPageNumberLimit(minPageNumberLimit + pageNumberLimit);
      }
    }
  };

  //pagination end

  //edit function for the province
  const handleEdit = (id) => {
    dispatch({ type: branchConstants.BRANCH_EDIT_SUCCESS, payload: id });
    history.push("/create-branch");
  };
  const addPermission =
    group === "ADMIN" || group === "MAINTAINER" || is_superuser ? true : false;
  return (
    <>
      {branches?.length > 0 ? (
        <div className="mt-2">
          <div className="table-scrollable">
            <table className="table table-hover table-bordered text-center">
              <thead>
                <tr>
                  <th scope="col" style={{ width: "8%" }}>
                    S.N
                  </th>
                  <th scope="col">Branch Name</th>
                  <th scope="col">Code</th>
                  <th scope="col">Active</th>
                  <th scope="col">Action</th>
                </tr>
              </thead>
              <tbody>
                {branches?.length > 0 &&
                  branches.map((branch, i) => {
                    const { id, name, code, is_active } = branch;
                    return (
                      <tr key={id}>
                        <td className="fw-bold" style={{ width: "8%" }}>
                          {postsPerPage * (currentPage - 1) + (i + 1)}{" "}
                        </td>
                        <td>{name === "" ? "-" : name}</td>
                        <td>{code === "" ? "-" : code}</td>

                        <td>
                          <Status active={is_active} />
                        </td>

                        <td>
                          <Tooltip content="Edit">
                            <button
                              onClick={() => handleEdit(id)}
                              className="btn btn-sm btn-edit btn-primary mx-2"
                            >
                              <FaEdit />
                            </button>
                          </Tooltip>
                        </td>
                      </tr>
                    );
                  })}
              </tbody>
            </table>
          </div>
          {count > 0 && (
            <PaginationBlock
              currentPage={currentPage}
              postsPerPage={postsPerPage}
              count={count}
              paginate={paginate}
              handleClick={handleClick}
              setCurrentPage={setCurrentPage}
              minPageNumberLimit={minPageNumberLimit}
              maxPageNumberLimit={maxPageNumberLimit}
            />
          )}
        </div>
      ) : (
        <NoData
          search={search}
          onClick={() => history.push("/create-branch")}
          addPermission={addPermission}
        />
      )}
    </>
  );
};

export default BranchList;
